.newsletter{
    padding: 2rem 4rem ;
    border: 1px solid var(--color-golden);
    background: var(--color-black);
    z-index: 1;
}
.newsletter .news-heading{
    text-align: center;
}
.newsletter .news-input{
    display: flex;
    flex-direction: row;
    margin-top: 2rem;
}
.newsletter .news-input input{
    width: 620px;
    border: 1px solid var(--color-golden);
    border-radius: 5px;
    font-size: 1rem;
    font-family: var(--font-base);
    color: var(--color-white);
    margin-right: 2rem;
    padding: 0.75rem 1rem;
    background: var(--color-black);
}
.newsletter .news-input button{
    width: max-content;
}
@media screen and (min-width:2000px){
    .newsletter .news-input input{
        font-size: 2rem;
    }
}
@media screen and (max-width:900px){
    .newsletter .news-input{
        flex-direction: column;
        width: 100%;
        align-items: center;
    }
    .newsletter .news-input input{
        margin: 0 0 2rem 0;
        width: 100%;
    }
    
}
@media screen and (max-width:650px){
    .newsletter{
        padding: 2rem 0;
        border: none;
    }
    
}
@media screen and (max-width:300px){
    .newsletter .news-heading h1{
        font-size: 32px;
        line-height: 50px;
    }
    
}